import { Component, OnInit } from '@angular/core';
import { MouseEvent } from '@agm/core';
import { Title } from '@angular/platform-browser';
import { ContactUsService } from '../../services/contact-us.service';
import { FormBuilder, AbstractControl, FormGroup, FormControl, Validators } from '@angular/forms';
declare var $: any;
@Component({
  selector: 'app-contactus',
  templateUrl: './contactus.component.html',
  styleUrls: ['./contactus.component.scss']
})

export class ContactusComponent implements OnInit {

    title = 'Contact Numbers & Office Locations | CivilSoft HCM';
    contactForm: FormGroup;
    submitted = false;
    NewMail: object;
   
    
    constructor(private titleService: Title, private formBuilder: FormBuilder, private sendMail: ContactUsService) { }

    ngOnInit(): void {
        this.titleService.setTitle(this.title);
        this.contactForm = this.formBuilder.group({
            fullName: ['', Validators.required],
            email: ['', [Validators.required, Validators.email]],
            subject: ['', Validators.required],
            message: ['', Validators.required],
       
        });

    }
    // convenience getter for easy access to form fields
    get f() { return this.contactForm.controls; }

    onSubmit() {
        this.submitted = true;
        // stop here if form is invalid
        if (this.contactForm.invalid) {
            return;
        }
        //form submitted
        //alert('SUCCESS!! :-)')
        $('#ScheduleModal').modal('hide');
       // $('#SuccessPopup').modal('show');

        //this.contactForm.reset();
        let FormMail = JSON.stringify(this.contactForm.value);
        this.sendMail.SetNewMail(FormMail);
    }
    // google maps zoom level
    zoom: number = 4;

    // initial center position for the map
    lat: number = 31.231180799999997;
    lng: number = 29.956505600000003;

    clickedMarker(label: string, index: number) {
        console.log(`clicked the marker: ${label || index}`)
    }

    mapClicked($event: MouseEvent) {
        this.markers.push({
            lat: $event.coords.lat,
            lng: $event.coords.lng,
            draggable: true
        });
    }

    markerDragEnd(m: marker, $event: MouseEvent) {
        console.log('dragEnd', m, $event);
    }

    markers: marker[] = [
        {
            lat: 25.257441 ,
            lng: 55.337158 ,
            label: 'A',
            draggable: true
        },
        {
            lat: 31.231180799999997 ,
            lng: 29.956505600000003 ,
            label: 'B',
            draggable: true
        },
        {
            lat: 21.575240,
            lng: 39.143220,
            label: 'C',
            draggable: true
        }
    ]

}

interface marker {
    lat: number;
    lng: number;
    label?: string;
    draggable: boolean;
}